import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,Modal,ModalController,Events } from 'ionic-angular';
import { BlogProvider } from '../../../providers/providers';

/**
 * Generated class for the QuestionGroupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-question',
  templateUrl: 'question.html',
})
export class QuestionPage {

  question:any
  modal:Modal;
  showModal = false;
  questionGroup:any

  constructor(public navCtrl: NavController,
     public navParams: NavParams,
     public modalCtrl:ModalController,
     public blogProvider:BlogProvider,
     public events:Events) {
    this.question=this.navParams.data;
    this.questionGroup=this.question.questionGroup
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad QuestionGroupPage');
  }
  hideAnswers(item){
    item.isShowAnswers=false;
  }
  showAnswers(item){
    item.isShowAnswers=true;
  }
  openModal(item){
    if(this.modal&&this.showModal){
      this.modal.dismiss();
    }else{
      this.modal = this.modalCtrl.create('AnswerAddPage', {item}, {
        showBackdrop: true,
        enableBackdropDismiss: true,
        enterAnimation: 'modal-from-bottom-enter',
        leaveAnimation: 'modal-from-bottom-leave'
      });
      this.modal.onDidDismiss(data => {
        this.showModal = false;
      });
      this.modal.present().then(data => {
        this.showModal = true;
      });     
    }   
  }
  addQuestion(){
    this.navCtrl.push("QuestionAddPage");
  }
}


